
import { mapActions } from 'vuex'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Group from 'vux/src/components/group/index.vue'

import { commonComponentMixin } from 'common/js/mixin.js'
import { FlowAction } from 'api/index.js'

export default {
mixins: [commonComponentMixin],
data () {
return {
MindInfo: '',
formState: '',
Current: {},
HistoryMind: [],
afterSaveMsg: '',
mx_isLoading: false,
mx_message: '',
mx_alertShow: false,
mx_alertTitle: '提示',
mx_toastShow: false,
mx_deleyTime: 1000
}
},
mounted () {
this.getStopFlowData()
},
methods: {

submitData () {
let valid = this.validSubmitData()

if (!valid.isPass) {
this.MixinAlertShowEvent(valid.msg)
return false
}

let params = Object.assign({}, {
Current: this.Current,
FlowOperate: 'Stop',
MindInfo: this.MindInfo,
VoteText: '',
VoteValue: ''
})

this.MinXinHttpFetch(FlowAction(JSON.stringify(params)), (response) => {
if (response.success) {
this.afterSaveMsg = '完成提交'
this.mx_toastShow = true

this.GetInformCount().then(() => {
if (this.timer) {
clearTimeout(this.timer)
}

this.timer = setTimeout(() => {
if (this.formState === 'view') {
this.$router.push('/workinfos')
} else {
this.goBack()
}
}, 1000)
}).catch((e) => {
this.AlertShowEvent(e.message)
})
}
})
},

validSubmitData () {
let obj = {
msg: '',
isPass: true
}

if (this.MindInfo === '') {
obj.msg = '意见不能为空'
obj.isPass = false
return obj
}

return obj
},

getStopFlowData () {
this.getQuery()
let params = Object.assign({}, {
SubOperate: 'GetCurrentSeque',
FlowOperate: 'SupplyInstance',
SequeID: this.query.SequeID,
Current: this.query
})

this.MinXinHttpFetch(FlowAction(JSON.stringify(params)), (response) => {
if (response.success) {
let value = response.data.value

this.Current = Object.assign({}, value.Current)
this.HistoryMind = value.HistoryMind.concat()
}
})
},

getQuery () {
let query = this.$router.history.current.query
if (query) {
this.query = Object.assign({}, query)
this.formState = this.query.FormState
}
},

goBack () {
this.$router.back()
},
...mapActions([
'GetInformCount',
'AlertShowEvent'
])
},
components: {
XTextarea,
Group
}
}



